.about {
  padding-top: 125px;
  padding-bottom: 40px;
  background-color: $bg-color;
}

.about__pic-wrap {
  @media screen and (min-width: 1280px) {
    width: 1280px;
    margin: 0 auto;
    background-image: url(/images/about/about-milk-desktop.png);
    background-size: contain;
    background-position: right bottom;
    background-repeat: no-repeat;

    @media screen and (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/images/about/about-milk-desktop2x.png);
    }
  }
}

.about__pre-title {
  margin-bottom: 15px;

  font-family: Titan One;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.17;

  text-align: center;
  text-transform: uppercase;

  color: $primary-title-color;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.about__title {
  margin-bottom: 26px;

  font-family: Titan One;
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  text-transform: uppercase;

  color: $secondary-title-color;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
  }
}

.about-wrap {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.about__pic {
  max-width: 100%;
  height: 280px;

  background-image: url(/images/about/about-cow-mobile.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  @media screen and (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(/images/about/about-cow-mobile2x.png);
  }

  @media screen and (min-width: 768px) {
    min-width: 340px;
    min-height: 340px;

    background-image: url(/images/about/about-cow-tablet.png);

    @media screen and (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/images/about/about-cow-tablet2x.png);
    }
  }

  @media screen and (min-width: 1280px) {
    min-width: 516px;
    min-height: 516px;

    background-image: url(/images/about/about-cow-desktop.png);

    @media screen and (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/images/about/about-cow-desktop2x.png);
    }
  }
}

.about__description {
  padding-top: 46px;
  padding-left: 50px;
  padding-right: 50px;

  @media screen and (max-width: 767px) {
    background-image: url(/images/about/about-milk-mobile.png);
    background-size: contain;
    background-position: 50% 85%;
    background-repeat: no-repeat;

    @media screen and (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/images/about/about-milk-mobile2x.png);
    }
  }
}

.about__description-title {
  margin-bottom: 45px;

  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.59;

  color: $secondary-text-color;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.79;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.75;
  }
}

.about__description-paragraph {
  margin-bottom: 17px;

  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.59;

  color: $third-text-color;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.79;
  }

  @media screen and (min-width: 1280px) {
    font-size: 14px;
    line-height: 1.93;
  }
}

.about__button {
  width: 176px;
  border-radius: 20px;
  padding: 7px;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.31;
  }

  text-align: center;
  background: #ffffff;
  color: #d41443;
  border-color: transparent;
  cursor: pointer;
}

.about__button:hover {
  background: #ecebeb;
}

.about__button::after {
  content: '';
  border: solid #d41443;
  border-width: 0 2px 2px 0;
  margin-left: 15px;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
